<template lang="pug">
  #app
    p.bg-primary.w-100.p-2.text-center.text-white.mb-0 Este cuestionario es una prueba!
    nav.navbar.navbar-toggleable-md.navbar-light.bg-faded
      button.navbar-toggler.navbar-toggler-right(type='button', data-toggle='collapse', data-target='#navbarNavAltMarkup', aria-controls='navbarNavAltMarkup', aria-expanded='false', aria-label='Toggle navigation')
        span.navbar-toggler-icon
      a.navbar-brand.font-weight-bold(href='#') David Supo
      #navbarNavAltMarkup.collapse.navbar-collapse
        .navbar-nav
          a.nav-item.nav-link(href='#')
          a.nav-item.nav-link(href='#') Sistemas
          a.nav-item.nav-link(href='#') Pricing
    .w-100
      .jumbotron
        h1.display-3.text-white.font-weight-bold.col-md-6 
          | People 
          span.text-title Progress
          | !
        p.lead.text-white.col-md-6.mt-4
          | Esta es una demostración del primer cuestionario, una vez terminado dar click en el botón FINALIZAR y se resolverá automaticamente.
    template(v-if="!showProfile")
      .container.d-flex.flex-wrap.justify-content-center
        .card.col-md-5.m-3.pb-4(v-for="question in questions")
          .card-block.p-0.pt-3
            h4.card-title.mb-3 PREGUNTA {{question.id}}
            .card-text.m-0.mb-1.d-flex.align-items-center
              p.m-0.col-6
              p.m-0.col-3.d-flex.align-items-center.justify-content-center.font-weight-bold MAS
              p.m-0.col-3.d-flex.align-items-center.justify-content-center.font-weight-bold MENOS

            .card-text.m-0.mb-1.d-flex.align-items-center(v-for="(alternativa,index) in question.alternativas")
              p.m-0.col-6 {{alternativa.name}}
              .m-0.col-3.d-flex.align-items-center.justify-content-center
                label.contenidor
                  input#option1(type='radio', :name="'masoption'+question.id" :value="alternativa.type" v-model="question.masresponse")
                  span.checkmark

              .m-0.col-3.d-flex.align-items-center.justify-content-center
                label.contenidor
                  input#option1(type='radio', :name="'menosoption'+question.id" :value="alternativa.type" v-model="question.minresponse")
                  span.checkmark
      .container
        button.btn.btn-danger.mb-5.ml-3(@click="Corregir") FINALIZAR
    
    .container.mt-4(v-if="showProfile")
      h1.w-100.text-center.font-weight-bold.text-uppercase Patrón del {{this.details.id}}
      p
        span.font-weight-bold Emociones : 
        | {{this.details.detalle.emocion}}
      p
        span.font-weight-bold Meta : 
        | {{this.details.detalle.meta}}
      p
        span.font-weight-bold Juzga a los demás por : 
        | {{this.details.detalle.juzga}}
      p
        span.font-weight-bold Influye en los demás mediante : 
        | {{this.details.detalle.influye}}
      p
        span.font-weight-bold Su valor para la organización : 
        | {{this.details.detalle.valor}}
      p
        span.font-weight-bold Abusa de : 
        | {{this.details.detalle.abusa}}
      p
        span.font-weight-bold Bajo presión : 
        | {{this.details.detalle.presion}}
      p
        span.font-weight-bold Teme : 
        | {{this.details.detalle.teme}}
      p
        span.font-weight-bold Sería más eficaz si : 
        | {{this.details.detalle.eficaz}}
      p.font-weight-bold.w-100 Observaciones :
      p(v-for="observacion in this.details.detalle.observaciones")
        | {{observacion.descripcion}}
      button.btn.btn-success.mb-4.mt-3(@click="Limpiar") REALIZAR OTRO CUESTIONARIO
</template>

<script>
import swal from 'sweetalert2'
import {getDetails} from './store/functions'
import {questions} from './store/questions'
export default {
  name: 'app',
  data(){
    return{
      questions:[],
      details:{},
      showProfile:false,
      intentos:5
    }
  },
  created(){
    this.questions = questions;    
    this.intentos = localStorage.getItem('countTry')? parseInt(localStorage.getItem('countTry')) : 8;    
  },
  methods:{
    Limpiar(){
      this.questions.forEach(el=>{
        el.masresponse=-1;
        el.minresponse=-1;
      })
      // this.details.detalle.emocion= '';
      this.showProfile=false;
      swal(
        'Tenlo Presente!',
        `Recuerda, te quedan ${this.intentos} intentos más!`,
        'warning'
      )
      
    },
    Corregir(){
      let masCont=0;
      let minCont=0;
      let disc_d=0;
      let disc_i=0;
      let disc_s=0;
      let disc_c=0;

      this.questions.forEach(el=>{
        if(el.masresponse>=0){
          if(el.masresponse==0){disc_d++}
          if(el.masresponse==1){disc_i++}
          if(el.masresponse==2){disc_s++}
          if(el.masresponse==3){disc_c++}
          masCont++;
        }
        if(el.minresponse>=0){     
          if(el.minresponse==0){disc_d--}
          if(el.minresponse==1){disc_i--}
          if(el.minresponse==2){disc_s--}
          if(el.minresponse==3){disc_c--}     
          minCont++;
        }
      })
      
      if(masCont==this.questions.length && minCont==this.questions.length){
        if(this.intentos>0){
          let disc =[disc_d,disc_i,disc_s,disc_c];
          this.details = getDetails(disc);
          this.intentos--;
          localStorage.setItem('countTry',this.intentos.toString());
          this.showProfile = true;
          swal(
            'Bien!',
            `Recuerda, te quedan ${this.intentos} intentos más!`,
            'success'
          )
        }else{
          swal(
            'Ops!',
            'Se acabo tu versión de prueba!',
            'error'
          )
        }
        
      }else{
        swal(
          'Ops!',
          'Debes contestar todas las preguntas!',
          'error'
        )
      }
    },

    checkedInput() {
      let el = document.getElementById('option1');
    }
  }
}
</script>

<style lang="scss">
.btn{
  cursor:pointer
}
.jumbotron{
  background:linear-gradient(-45deg,#7A38B9,#5834A1);
  border-radius: 0;
}
.text-title{
  color:#DAA520;
}


.contenidor {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 22px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.contenidor input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
}

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 25px;
    width: 25px;
    background-color: #eee;
    border-radius: 50%;
}

.contenidor:hover input ~ .checkmark {
    background-color: #ccc;
}

.contenidor input:checked ~ .checkmark {
    background-color: #2196F3;
}

.checkmark:after {
    content: "";
    position: absolute;
    display: none;
}

.contenidor input:checked ~ .checkmark:after {
    display: block;
}

.contenidor .checkmark:after {
 	top: 9px;
	left: 9px;
	width: 8px;
	height: 8px;
	border-radius: 50%;
	background: white;
}

</style>



