import {perfiles} from './perfiles'
import {detalles} from './detalles'

function getProfile(disc){
  let patron = getPatron(disc);
  let perfil = perfiles.find(val=>val.id==patron);
  return perfil;
}

function getDetails(disc){
  let perfil = getProfile(disc);
  let details = detalles.find(val=>val.id==perfil.perfil);
  return details;
}
function getPatron(disc){
  let valor_d = 0;
  let disc_d = disc[0];
  // =SI(Q34<-7;1;
  // SI(Y(Q34<-3;Q34>-8);2;
  // SI(Y(Q34<0;Q34>-4);3;
  // SI(Y(Q34<2;Q34>-1);4;
  // SI(Y(Q34>1;Q34<5);5;
  // SI(Y(Q34>4;Q34<9);6;
  // SI(Q34>8;7)))))))

  if(disc_d<-7){valor_d=1}
  else if(disc_d<-3){valor_d=2}
  else if(disc_d<0){valor_d=3}
  else if(disc_d<2){valor_d=4}
  else if(disc_d<5){valor_d=5}
  else if(disc_d<9){valor_d=6}
  else valor_d=7

  let valor_i=0;
  let disc_i = disc[1];

  // =SI(R34<-7;1;
  // SI(Y(R34<-3;R34>-8);2;
  // SI(Y(R34<-1;R34>-4);3;
  // SI(Y(R34<2;R34>-2);4;
  // SI(Y(R34>1;R34<4);5;
  // SI(Y(R34>3;R34<7);6;
  // SI(R34>6;7)))))))

  if(disc_i<-7){valor_i=1}
  else if(disc_i<-3){valor_i=2}
  else if(disc_i<-1){valor_i=3}
  else if(disc_i<2){valor_i=4}
  else if(disc_i<4){valor_i=5}
  else if(disc_i<7){valor_i=6}
  else valor_i=7

  let valor_s = 0;
  let disc_s=disc[2];

  // =SI(S34<-10;1;
  // SI(Y(S34<-6;S34>-11);2;
  // SI(Y(S34<-3;S34>-7);3;
  // SI(Y(S34<0;S34>-4);4;
  // SI(Y(S34>-1;S34<3);5;
  // SI(Y(S34>2;S34<8);6;
  // SI(S34>7;7)))))))

  if(disc_s<-10){valor_s=1}
  else if(disc_s<-6){valor_s=2}
  else if(disc_s<-3){valor_s=3}
  else if(disc_s<0){valor_s=4}
  else if(disc_s<3){valor_s=5}
  else if(disc_s<8){valor_s=6}
  else valor_s=7

  let valor_c = 0;
  let disc_c = disc[3];

  // =SI(T34<-5;1;
  // SI(Y(T34<-2;T34>-6);2;
  // SI(Y(T34<0;T34>-3);3;
  // SI(Y(T34<3;T34>-1);4;
  // SI(Y(T34>2;T34<5);5;
  // SI(Y(T34>4;T34<9);6;
  // SI(T34>8;7)))))))

  if(disc_c<-5){valor_c=1}
  else if(disc_c<-2){valor_c=2}
  else if(disc_c<0){valor_c=3}
  else if(disc_c<3){valor_c=4}
  else if(disc_c<5){valor_c=5}
  else if(disc_c<9){valor_c=6}
  else valor_c=7

  return `${valor_d}${valor_i}${valor_s}${valor_c}`
}

export {getProfile,getDetails,getPatron}


